/* Shared Layout Styles
 * ========================================================================== */
body {
  margin-top: 20px;
  min-width: 320px;
}

[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important;
}

.container {
  max-width: 730px;
}

.caret-reverse {
  transform: rotate(180deg);
}

.action-bar {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.1);
  padding: 5px 15px;
  font-size: 0.8em;
  border-radius: 0 0 0 5px;
}

.action-bar > a {
  color: black;
  text-decoration: none;
  opacity: 0.7;
}

.action-bar > a:hover {
  opacity: 1;
  transform: translateX(0);
}

.no-touch .action-bar {
  display: none;
}

.list-group-item:hover .action-bar {
  display: block;
}

.list-group-item-heading {
  margin-bottom: 0;
  cursor: pointer;
}

#todolist-items .checkbox {
  margin-top: 0;
  margin-bottom: 0;
}

.todo-completed {
  opacity: 0.5;
}

.todo-completed .list-group-item-heading {
  text-decoration: line-through;
}

.progress-bar[aria-valuenow="0"] {
  color: #000;
}

.glyphicon-refresh-animate {
  animation: spin 0.7s infinite linear;
}

@keyframes spin2 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

