   ========================================================================== */
.u-clear {
  clear: both;
}

.u-centered {
  margin-right: auto;
  margin-left: auto;
  float: none;
}

.u-float,
.u-float--left {
  float: left;
}

.u-float--right {
  float: right;
}

.u-inline > *, .u-inline--baseline > * {
  display: inline-block;
}

.u-stacked > * {
  display: block;
}

.u-inline--baseline > * {
  vertical-align: baseline;
}

